<template>
  <div id="app">
    <router-view></router-view>
    <ul class="footer">
      <router-link tag='li' v-for="(item,index) in route" :to=item.url :key=index><i :class=item.icon></i></router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      route: [
      {
        url: '/lose',
        icon: 'el-icon-edit'
      },{
        url: '/pick',
        icon: 'el-icon-delete'
      },{
        url: '/plus',
        icon: 'el-icon-plus'
      }, {
        url: '/info',
        icon: 'el-icon-date'
      },{
        url: '/user',
        icon: 'el-icon-search'
      }]
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
}
*{
  margin: 0;
  padding: 0;
}
.footer{
  position: fixed;
  bottom: 0px;
  width:100%;
  text-align: center;
  height: 50px;
  border-top: 1px solid #dcdfe0;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
}
.footer li{
  list-style: none;
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin-top: 10px;
  border: 1px solid #52bdf5;
  color: #52bdf5;
  border-radius: 50%;
  /*cursor: pointer;*/
}
.footer li:first-child{
  margin-left: 20px;
}
.footer li:last-child{
  margin-right: 20px;
}
.routeActive{
  background-color: #52bdf5;
  color: #fff;
}
.routeActive i{
  color: #fff;
}
</style>



