<template>
  	<div class="lost">
  		<div class="search-wrapper">
  			<el-select v-model="dist" placeholder="请选择">
  				<el-option
  				v-for="item in province"
  				:key="item"
  				:label="item"
  				:value="item"></el-option>
  			</el-select>
  			<el-input v-model="words" placeholder="物品/位置/关键字"></el-input>
  			<el-button type="warning">搜索</el-button>
  		</div>
  		<div class="dist-zone">
  			<div class="dist-left">
	  			<span class="icon"><i class="el-icon-fa-map-marker"></i></span>	
	  			<span class="dist">{{msg.distname}}</span>
	  			<span class="news">最新动态{{msg.number}}条</span>
	  		</div>
	  		<div class="dist-right">
	  			<i class="el-icon-fa-angle-right"></i>
	  		</div>
  		</div>
  		<div class="content">
  			<div class="lose-info" v-for="item in infoMsg">
  				<div class="info-left">
  					<img :src=item.icon alt="用于头像" width="40" height="40">
  					<!-- <i :class=item.icon></i> -->
  				</div>
  				<div class="info-right">
  					<div class="name">{{item.name}}</div>
  					<div class="text">{{item.text}}</div>
  					<div class="photo" v-show="item.photos.length>0">
  						<img :src="value" alt="失物照片" v-for="value in item.photos" width="65" height="65">
  					</div>
  					<div class="other">
  						<div class="left">
  							<i class="el-icon-fa-map-marker"></i> {{item.from}}
  						</div>
  						<div class="right">
  							<span class="like"><i class="el-icon-fa-heart-o"></i> {{item.like}}</span>
  							<span class="comment"><i class="el-icon-fa-commenting"></i> {{item.comment}}</span>
  						</div>
  					</div>
  				</div>
  			</div>
  		</div>
  	</div>
</template> 
 
<script>
import UserIcon1 from '../assets/avatar1.jpg'
import UserIcon2 from '../assets/avatar2.png'
import UserIcon3 from '../assets/avatar3.png'
import Photo1 from '../assets/p1.jpg'
import Photo2 from '../assets/p2.jpg'
import Photo3 from '../assets/p3.jpg'
export default { 

  data () { 
    return { 
 		msg: {
 			distname: '港湾中心',
 			number: 398
 		},
 		province: ["北京","上海","重庆","天津"],
 		dist: '',
 		words: '',
 		infoMsg: [
 			{
 				icon: UserIcon1,
 				name: '吴佳乐',
 				text: '今天丢了钱包，55555 黑色的，真皮的，里面有身份证，银行卡，还有很多饭票，请拾到者联系我',
 				photos: [Photo1, Photo2, Photo3],
 				from: '海上餐厅',
 				like: 23,
 				comment: 180

 			},
 			{
 				icon: UserIcon2,
 				name: '艾斯',
 				text: '在滨江道与弟弟走失，我的弟弟带一个草帽，叫路飞，喜欢吃东西，喜欢冒险，走失时身穿红色的小坎肩，蓝色的5分牛仔裤，脚穿草鞋，梦想是找到OnePiece，当上海贼王。',
 				photos: [],
 				from: '滨江道',
 				like: 23,
 				comment: 180

 			},
 			{
 				icon: UserIcon3,
 				name: '白胡子',
 				text: '顶上战争被战国设计暗算身受重伤，随后被数万精英海军围殴积累了大量的伤害，最后因受伤过重后被伺机出手的黑胡子马歇尔·D·帝奇一伙乱枪扫射杀害，并被黑胡子夺走震震果实的能力。',
 				photos: [],
 				from: '白胡子海贼团',
 				like: 23,
 				comment: 180

 			}
 		]
    }; 
  },

}; 
</script> 
 
<style scoped> 
.lost{
	background: linear-gradient(top, #e7eff2, #f2f6f7);
	margin-bottom: 61px;
}
.search-wrapper{
	width:auto;
	height: 50px;
	line-height: 50px;
	margin: 0 auto;
	padding: 0 3px;
	background: #62caf7;
}
.search-wrapper > .el-select{
	width: 25%;
}
.search-wrapper > .el-button{
	width: 16%;
}
.search-wrapper > .el-input{
	width: 56%;
}
.dist-zone{
	height: 60px;
	line-height: 60px;
	padding-left: 10px;
	border-bottom: 1px solid #e0e9ee;
	background-color: #fff;
}
.dist-zone .icon > i{
	color:#58c2f6;
	font-size: 20px !important;
}
.dist-zone .dist{
	color: #4d4d4d;
	font-size: 20px;
	padding-left: 6px;
}
.dist-zone .news{
	color: #808080;
	font-size: 16px;
	padding-left: 10px;

}
.dist-left{
	float: left;
}
.dist-right{
	float: right;
	color: #999;
	padding-right: 10px;
}
.dist-right>i{
	font-size: 20px !important;
}
.content{
	
}
.lose-info{
	margin-top: 10px;
	background-color: #fff;
	padding: 10px;
	display: flex;
	flex-flow: row;
}
.lose-info .info-left{
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border-radius: 50%;
	margin-right: 6px;
	overflow:hidden;
}
.lose-info .info-right{
	flex:1;
}
.info-right .name{
	padding: 8px 0;
	font-size: 16px;
	color: #4d4d4d;
}
.info-right .text{
	margin-bottom: 10px;
	color: #b3b3b3;
	font-size: 12px;
}
.info-right .text .photo{

}
.info-right .photo img{
	margin-right: 10px;
}
.other{
	height: 10px;
	line-height: 10px;
	padding: 10px 0;
	color: rgba(153,153,153,0.4);
}
.other i{
	color: #4fbbf5;
	margin-right: 2px;
}
.other .left{
	float: left;
	font-size: 12px;
}
.other .left i{
	color: #4fbbf5;
	margin-right: 5px;
}
.other .right{
	float: right;
	font-size: 10px;
}
.other .right i{
	font-size: 10px !important;
}
.other .like{
	margin-right: 8px;
}
</style> 


